
@import url('variables.css');

.nav__bar-link.active{
    color: var(--color-main);
}

.nav__bar-link.active::before {
    content: '';
    height: 5px;
    background-color: var(--color-main);
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    border-radius: 10px;
}

